<template>
  <div class="main accueil" :style="image" @click="goHome">
    <!-- <div class="mouse"></div> -->
    <div class="touchscreen" :style="secondary"></div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      img: {
        main: require("@/assets/tests/Accueil.png"),
        secondary: require("@/assets/tests/touch.png"),
      },
    };
  },
  beforeMount() {},
  mounted() {},
  methods: {
    goHome() {
      this.$routeToLoad.set({ name: "Home" });
      this.$router.push({name: "Home" })
    },
  },
  watch: {},
  computed: {
    image() {
      return `background-image:url(${this.img.main})`;
    },
    secondary() {
      return `background-image:url(${this.img.secondary})`;
    },
  },
};
</script>

<style lang="scss" scoped>
.main {
  width: 100%;
  height: 100%;
  background-position-y: 100%;
  position: relative;
}

.touchscreen{
  width: 858px;
  height: 145px;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 120px;
  z-index: 10000;
}

.accueil{
  z-index: 9999;
  position: fixed;
  top: 0;
}
</style>
