<template>
  <div class="main" :style="image">
    <div class="buttons">
      <img
        @click="goto({ name: 'Page', params: { id: 'caractech' } })"
        :src="img.secondary.caracs"
        draggable="false" 
      />
      <img
        @click="goto({ name: 'Page', params: { id: 'focusgnl' } })"
        :src="img.secondary.focus"
        draggable="false" 
      />
    </div>
    <div class="retour">
      <img 
      @click="goHome"
        :src="img.secondary.retour"
        draggable="false" 
      />
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      img: {
        main: require("@/assets/menus/Decouvrir_.png"),
        secondary: {
          caracs: require("@/assets/menus/Group 10 (1).png"),
          focus: require("@/assets/menus/Group 12.png"),
          retour: require("@/assets/tests/retour.png"),
        },
      },
    };
  },
  beforeMount() {},
  mounted() {},
  methods: {
    goto(route) {
      this.drawer = false;
      // this.$router.push(route)
      this.$routeToLoad.set(route);
      document.dispatchEvent(new CustomEvent("loadroute"));
    },
    goHome() {
      this.$routeToLoad.set({ name: "Home" });
      document.dispatchEvent(new CustomEvent("loadroute"));
    },
  },
  watch: {},
  computed: {
    image() {
      return `background-image:url(${this.img.main})`;
    },
  },
};
</script>

