<template>
<div>
    <v-app-bar
      style="z-index: 9"
      color="#E3000B"
      dark
      height="139"
      class="header"
    >
      <v-app-bar-nav-icon class="menubtn" @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title style="font-size: 28px">MENU</v-toolbar-title>
      <v-toolbar-items style="font-size: 28px;position:absolute;left:50%;transform:translate(-50%);align-items:center">{{pageCount}}</v-toolbar-items>

      <v-spacer></v-spacer>

      <img @click="goHome" draggable="false" :src="require('@/assets/tests/logo.png')" width="312" height="88" class="logo"/>
    </v-app-bar>

    <img :src="require('@/assets/tests/bande_haut.svg')" style="position: absolute; top:23px;left:23px;pointer-events:none;z-index:10"/>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      bottom
      :overlay-opacity="0.9"
      height="942"
      width="1051"
      style="background: #E3000B;margin-top:138px;z-index:16;height: 100%"
    >
    <div :style="'position:absolute;top:2px;right: 30px;background-image: url('+require('@/assets/tests/close.png')+') ; height:48px;width: 48px'" @click.stop="drawer = !drawer"></div>
    <div style="height: 100%;padding: 180px 42px 262px 132px;color:white">
      <button @click="goto({name: 'Company'})"><div style="font-size:50px;font-weight: 900"> LA COMPAGNIE </div></button>
      <div class="line"></div>
      <button @click="goto({name: 'Discover'})"><div style="font-size:50px;font-weight: 900"> DÉCOUVRIR A GALEOTTA </div></button>
      <div class="line"></div>
      <button @click="goto({name: 'JoinUs'})"><div style="font-size:50px;font-weight: 900"> NOUS REJOINDRE </div></button>
      <div class="line"></div>
    </div>
    <div style="position:absolute;width: 100%;height:5px; bottom:calc(138px + 18px); left:23px;background:white;"></div>
    </v-navigation-drawer>
    <div v-show="drawer" style="position:fixed;width: 23px;height:5px; bottom:18px; left:0;background:#E3000B;z-index:18"></div>
    <div v-show="drawer" style="position:absolute;height: auto;width: 5px; left: 23px;top: 138px;bottom:19px;background:white;z-index:17"></div>
    <div v-show="drawer" @click.stop="drawer = false" style="width: 100%;height:100%;position:fixed;top:0;left:0;background:white;opacity: 0.8;z-index:1"></div>
</div>
</template>

<script>
  export default {
    data: () => ({
      drawer: false,
      group: null,
    }),

    methods: {
      goto(route){
        this.drawer = false;
        // this.$router.push(route)
        this.$routeToLoad.set(route)
        document.dispatchEvent(new CustomEvent("loadroute"))
      },
      goHome(){
        this.$routeToLoad.set({name: "Home"})
        document.dispatchEvent(new CustomEvent("loadroute"))
      }
    },
    computed:{
      pageCount(){
        if(this.$route.name != "Page") return ""
        else {
          switch(this.$route.params.id){
            case 'activite1': 
              return "1 / 4"
            case 'activite2': 
              return "2 / 4"
            case 'activite3': 
             return "3 / 4"
            case 'activite4': 
              return "4 / 4"
            case 'ambition1': 
              return "1 / 5"
            case 'ambition2': 
              return "2 / 5"
            case 'ambition3': 
              return "3 / 5"
            case 'ambition4': 
              return "4 / 5"
            case 'ambition5': 
              return "5 / 5"
            default: 
              return "";
          }
        }
      }
    },

    watch: {
      group () {
        this.drawer = false
      },
    },
  }
</script>

