<template>
    <div class="main" :style="page_"> 
    </div>
</template>

<script>

export default {
  components: {
  },
  data() {
    return {
      page: null,
      data: {
        Activite1: {
          main: require("@/assets/pages/Slide 4 - Notreactivite.png"),
          next: {name: 'Page', params: {id: 'activite2' } },
          prev: {name: 'Company'}

        },
        Activite2: {
          main: require("@/assets/pages/Slide 5 - Notre activité 11_.png"),
          next: {name: 'Page', params: { id: 'activite3' } },
          prev: {name: 'Page', params: { id: 'activite1' } }
        },
        Activite3: {
          main: require("@/assets/pages/Slide 5 - Notre activité 10_.png"),
          next: {name: 'Page', params: { id: 'activite4' } },
