        },
        Ambition1: {
          main: require("@/assets/pages/Slide 7 - Notre ambition 4.png"),
          next: {name: 'Page', params: { id: 'ambition2' } },
          prev: {name: 'Company'},
        },
        Ambition2: {
          main: require("@/assets/pages/Slide 8 - Notre ambition 14.png"),
          next: {name: 'Page', params: { id: 'ambition3' } },
          prev: {name: 'Page', params: { id: 'ambition1' } }
        },
        Ambition3: {
          main: require("@/assets/pages/Slide 8 - Notre ambition 19.png"),
          next: {name: 'Page', params: { id: 'ambition4' } },
          prev: {name: 'Page', params: { id: 'ambition2' } }
        },
        Ambition4: {
          main: require("@/assets/pages/Slide 8 - Notre ambition 16.png"),
          next: {name: 'Page', params: { id: 'ambition5' } },
          prev: {name: 'Page', params: { id: 'ambition3' } }
        },
        Ambition5: {
          main: require("@/assets/pages/Slide 8 - Notre ambition 20.png"),
          next: null,
          prev: {name: 'Page', params: { id: 'ambition4' } }
        },
        CaracTech: {
          main: require("@/assets/pages/Slide 11 - Caractéristiques technique du navire_.png"),
          next: null,
          prev: {name: 'Discover'}
        },
        FocusGNL: {
          main: require("@/assets/pages/Slide 13 - Focus GNL.png"),
          next: null,
          prev: {name: 'Discover'}
        },
        Officiers: {
          main: require("@/assets/pages/Slide 15 - Officiers de la marine marchande.png"),
          next: null,
          prev: {name: 'JoinUs'}
        },
        Spontanee: {
          main: require("@/assets/pages/spontanée_.png"),
          next: null,
          prev: {name: 'JoinUs'}
        }
      }
    };
  },
  beforeMount() {
  },
  mounted() {
    this.UpdatePage();
    document.addEventListener("routeloaded", this.UpdatePage);
  },
  beforeDestroy(){
    document.removeEventListener("routeloaded", this.UpdatePage);
  },
  methods: {
    UpdatePage(){
      switch(this.$route.params.id){
        case 'activite1': 
          this.page = this.data.Activite1;
          break;
        case 'activite2': 
          this.page = this.data.Activite2;
          break;
        case 'activite3': 
          this.page = this.data.Activite3;
          break;
        case 'activite4': 
          this.page = this.data.Activite4;
          break;
        case 'ambition1': 
          this.page = this.data.Ambition1;
          break;
        case 'ambition2': 
          this.page = this.data.Ambition2;
          break;
        case 'ambition3': 
          this.page = this.data.Ambition3;
          break;
        case 'ambition4': 
          this.page = this.data.Ambition4;
          break;
        case 'ambition5': 
          this.page = this.data.Ambition5;
          break;
        case 'caractech': 
          this.page = this.data.CaracTech;
          break;
        case 'focusgnl': 
          this.page = this.data.FocusGNL;
          break;
        case 'officier': 
          this.page = this.data.FocusGNL;
          break;
        case 'candidature': 
          this.page = this.data.Spontanee;
        break;
          default: 
           this.page = this.data.Spontanee;
           break;
        
        }


      this.$navigation.prev.set(this.page.prev)
      this.$navigation.next.set(this.page.next)
      document.dispatchEvent(new CustomEvent("updatenav"))
    }
  },
  beforeDestroy(){
    this.$navigation.prev.set(null)
    this.$navigation.next.set(null)
    document.dispatchEvent(new CustomEvent("updatenav"))
  },
  computed: {
    page_(){
      if(this.page == null) return
      return `background-image:url('${this.page.main}')`;
    }
  },
  watch: {
  },
};
</script>

